import Buslist from '../../components/common/buslist.vue'
function getDate(type) {
	const date = new Date();

	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();

	if (type === 'start') {
		year = year - 60;
	} else if (type === 'end') {
		year = year + 2;
	}
	month = month > 9 ? month : '0' + month;;
	day = day > 9 ? day : '0' + day;

	return `${year}-${month}-${day}`;
}
export default {	
	components:{
		Buslist
	},
	data(){
		return{
			list:['光谷大道五里湾','光谷大道金融港','光谷大三李陈','光谷大道关南村','光谷大道当代国际花园','光谷大道现代世贸中心'],
			xblist:['女','男'],
			xbcheck:0,
			classlist:["一年级","二年级","三年级","四年级","五年级","六年级"],
			classCheck:5,
			date: getDate({
				format: true
			}),
			startDate:getDate('start'),
			endDate:getDate('end'),
			time: '12:01',
			isNull:false,//是否有线路  true:无，false：有
			form:{
				name:'',
				sex:0,
				grade:1,
			}
		}
	},
	onLoad(){
		let data=uni.getStorageSync("addchildinfo")
		data=JSON.parse(data)
		this.form=data
		if(data.schoolId){
			this.getlinesList()
		}else{
			this.isNull=true
		}
		let userInfo=uni.getStorageSync("userInfo")
		if(userInfo){
			userInfo=JSON.parse(userInfo)
			this.form.parentId=userInfo.id
		}else{
			uni.showToast({
				icon:"none",
				title:"请登录后操作"
			})
		}
		
	},
	methods:{
		xbchange(val){
			console.log(val)
			// 性别改变
			this.form.sex=val.detail.value
		},
		classchange(val){
			// 年级改变
			this.form.grade=(val.detail.value*1)+1
			this.classCheck=val.detail.value
		},
		bindDateChange: function(e) {
			this.date = e.detail.value
			this.form.birthDate=e.detail.value
			console.log(this.form.birthDate)
		},
		bindTimeChange: function(e) {
			this.time = e.detail.value
		},
		goRouter(){
			if(this.isNull){
				//无线路  申报成功
				uni.navigateTo({
					url:"declaration"
				})
			}else{
				// 有线路  支付
				uni.navigateTo({
					url:"payment"
				})
			}
		},
		getlinesList(){
			// 获取线路
			this.$http.post("puProduct/getProductBySchoolId",{
				schoolId:this.form.schoolId
			}).then(res=>{
				if(res.code==100){
					if(res.code.length==0){
						this.isNull=true
					}else{
						this.isNull=false
						this.list=res.info
					}
				}
			})
		},
	}
}
